@font-face {
    font-family: "Circe";
    src: url("fonts/roboto/Roboto-Regular.otf") format("opentype"),
    url("fonts/roboto/Roboto-Regular.woff2") format("woff2"),
    url("fonts/roboto/Roboto-Regular.woff") format("woff");
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: "Circe";
    src: url("fonts/roboto/Roboto-Medium.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: "Circe";
    src: url("fonts/roboto/Roboto-Bold.otf") format("opentype"),
    url("fonts/roboto/Roboto-Bold.woff2") format("woff2"),
    url("fonts/roboto/Roboto-Bold.woff") format("woff");
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: "Nizhegorodskiy";
    src: url("fonts/Nizhegorodsky/Nizhegorodsky.otf") format("opentype");
    font-weight: normal;
    font-style: normal;
}

$blue: #124285;
$red: #D4344C;
$font: 'Roboto', sans-serif;
body p{
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  line-height: 32px;
  color: #333333;
  text-decoration: none;
}
.header{
  height: 112px;
  background: linear-gradient(90deg, #092040 0%, #092040 48.04%, #124285 100%);
  padding: 0 32px;
  overflow: hidden;
}
.header__logo-link {
  display: block;
  height: 112px;
  padding: 20px 0;
}
.header__logo {
  height: 100%;
  width: 100%;
  object-fit: contain;
  object-position: left;
}
.header__info-item {
  display: flex;
  align-items: center;
}
.header__first-title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: calc(8px + 8 * ((100vw - 320px) / 1043));
  line-height: 1.3;
  color: #FFFFFF;
  margin-bottom: 0;
  white-space: nowrap;
}
.header__first-description {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: calc(5px + 6 * ((100vw - 320px) / 1043));
  line-height: 1.3;
  color: #FFFFFF;
  margin-bottom: 0;
}

.header__info {
  display: flex;
  align-items: center;
  max-width: 1300px;
  height: 100%;
}
.header__sub-title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: calc(8px + 8 * ((100vw - 320px) / 1043));
  line-height: 1.3;
  color: #FFFFFF;
  text-align: right;
  margin-bottom: 0;
}
.header__lead_post {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: calc(5px + 6 * ((100vw - 320px) / 1043));
  line-height: 1.3;
  text-align: right;
  color: #FFFFFF;
  margin-bottom: 0;
}
.header__image_for_header {
  height: 112px;
  width: 136px;
  object-fit: contain;
  object-position: bottom;
}
.main-content {
  display: flex;
  height: 100%;
}
.sidebar {
  width: 400px;
  padding: 16px 32px;
  height: calc(100vh - 112px);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
  z-index: 10;
  overflow-y: scroll;
  scroll-behavior: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
.sidebar::-webkit-scrollbar {
  position: absolute;
  width: 0px;
}
.sidebar::-ms-track {
  position: absolute;
  width: 0px;
}
.sidebar::-moz-range-track {
  position: absolute;
  width: 0px;
}
.sidebar::-moz-viewport-scroll {
  position: absolute;
  width: 0px;
}
.sidebar::-webkit-scrollbar-track {

}
.sidebar::-webkit-scrollbar-thumb {
  background-color: rgba(239, 233, 230, 0.1);
}

.map-wrap {
  height: 100%;
  width: calc(100vw - 400px);
}
.mobile-map {
  display: none;
}

