  margin-top: 2px;
}


.category_btn {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  height: 64px;
  line-height: 20px;
  text-align: left;
  padding: 0 48px 0 16px;
  border: none;
  color: #fff;
  text-decoration: none;
  overflow: hidden;
}
.category_btn img {
  height: 32px;
  width: 32px;
  margin-right: 16px;
  opacity: 0.8;
}

.button {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  border-radius: 4px;
  font-family: 'Nizhegorodsky', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  height: 64px;
  line-height: 20px;
  text-align: left;
  padding: 0 48px 0 16px;
  border: none;
  background-color: #35383b;
  color: #fff;
  text-decoration: none;
  overflow: hidden;
  &.blue-btn {
    background-color: $blue;
    color: #fff;
    &:hover {
      color: #fff;
    }
  }
  &.red-btn {
    background-color: $red;
    color: #fff;
    &:hover {
      color: #fff;
    }
  }&.yellow-btn {
     background-color: #FAC06C;
     color: #fff;
     &:hover {
       color: #fff;
     }
   }
}
.small-btn {
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  font-family: $font;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  &.yellow-btn {
    background-color: #FAC06C;
    color: #fff;

